<template>
    <div class="sucess-log container z-depth-3 forgot">
        <h4 class="orange-text darken-2">
            C v nh bn  qun mt khu ca mnh?
        </h4>
        <h6>
            Hy in email ca bn vo link bn di, chng ti s gi n bn mt
            email gip khi phc mt khu.
        </h6>
        <div class="row">
                <div class="input-field col s6">
                    <input placeholder="Email ca bn" 
                            name="email"  
                            type="text" 
                            v-model="email">
                </div>
            </div>
            <submit-btn :isLoading="isLoading"
                        :disableCon="disabled"
                        @onSubmit="onSubmit">
                Kch hot
            </submit-btn>
        <!-- <a href="/auth/login">ng nhp</a> -->
        
    </div>
</template>

<script>
import submitBtn from '../../components/button/submitBtn'
export default {
    data() {
        return {
            email: '',
            isLoading: false,
        }
    },

    methods:{
        onSubmit(){
            //type, title, text, footer
            this.isLoading = true;
            fetch(`${process.env.VUE_APP_PORT}/user/forgot`, {
                method: 'POST',
                headers:{
                    'content-type':'application/json'
                },
                body: JSON.stringify({
                    email: this.email.toLowerCase()
                })
            }).then(resp=>{
                return resp.json()
            }).then(resData=>{
                if(resData.status === 'fail'){
                    this.firePopup('warning', 'Email khng tn ti', resData.msg)
                } else if (resData.status === 'success') {
                    this.firePopup('success', 'Thnh cng', resData.msg)
                }
                this.isLoading = false;
            }).catch(err=>{
                this.firePopup('error', 'Li server', err)
                throw err
            })
        },
    },

    computed:{
        disabled(){
            return this.email.length === 0 
        }
    },
    components:{
        submitBtn
    }
}
</script>

