<template>
    <div class="sucess-log container z-depth-3">
        <h4 class="orange-text darken-2">
            Reset password 
        </h4>
        <h6>
            Hy in mt khu mi ca bn  bn di (mt khu c  di t 7 k t tr ln)
        </h6>
        
        <div class="">
            <div class="input-field col s12">
                <input placeholder="Mt khu mi " 
                        name="password"  
                        type="password" 
                        v-model="password">
            </div>
            <div class="input-field col s12">
                <input placeholder="lp li mt khu " 
                        name="cfPass"  
                        type="password" 
                        v-model="cfPass">
            </div>
        </div>

        <div class="row">
            
        </div>
        <submit-btn :isLoading="isLoading"
                    :disableCon="!disabled"
                    @onSubmit="onSubmit">
            i mt khu
        </submit-btn>
        <!-- <a href="/auth/login">ng nhp</a> -->
        
    </div>
</template>

<script>
import submitBtn from '../../components/button/submitBtn'
export default {
    props:['token'], // token get from the router params
    data() {
        return {
            isLoading: false,
            password: '',
            cfPass: '',
        }
    },

    created(){
        this.checkValid();
    },

    methods: {
        onSubmit(){
            // do something
            this.isLoading = true;
            fetch(`${process.env.VUE_APP_PORT}/user/reset`,{
                method: 'PUT',
                headers: {
                    'content-type': 'application/json'
                },
                body:JSON.stringify({token: this.token, password: this.password})
            }).then(res=>{
                return res.json()
            }).then(resData=>{
                if(resData.status === 'success'){
                    this.firePopup('success', 'Thnh cng', resData.msg)
                    setTimeout(()=>{
                        this.$router.push('/auth/login')
                    }, 1500)
                }
                this.isLoading = false;

            }).catch(err=>{
                throw err
            })
        },
        checkValid(){
            //type, title, text, footer
            fetch(`${process.env.VUE_APP_PORT}/user/check-valid`,{
                method: 'POST',
                headers: {
                    'content-type': 'application/json'
                },
                body:JSON.stringify({token: this.token})
            }).then(res=>{
                return res.json()
            }).then(resData=>{
                if(resData.status === 'fail'){
                    this.firePopup('warning', 'Ht thi gian', 'Thi gian reset password ca ti khong ny  ht!')
                    setTimeout(()=>{
                        this.$router.push('/auth/login')
                    }, 1500)
                }
                
            }).catch(err=>{
                throw err
            })

        }
    },

    computed: {
        disabled(){
            // length check                  //samecheck
            if(this.password.length < 6 || this.password !== this.cfPass){
                return false
            } else {
                return true
            }
        }
    },

    components:{
        submitBtn
    }
}
</script>

<style lang="scss">
    .input-field{
        margin-left: 1rem;
    }
</style>

