<template>
    <div class="sucess-log container z-depth-3">
        <h4 class="cyan-text darken-4">
            Chc mng! Bn  ng k thnh cng!
        </h4>
        <h6>
            C mt email xc nhn va c gi n 
            email m bn  ng k, trong  c mt m dng
            th cc dch v trang web trong vng 7 ngy.
            bn hy dn n vo trng bn di. Xin cm n.
            Nu 5 pht m bn vn cha nhn c hy nhn
            <a href="#" @click.prevent.once="resend">Gi li (1 ln na)</a>,
            Bn c 10' trc khi m kch hot ht hn
        </h6>
        
        <div class="row">
            <div class="input-field col s6">
                <input placeholder="M kch hot" 
                        name="code"  
                        type="text" 
                        v-model="code">
            </div>
        </div>
        <submit-btn :isLoading="isLoading"
                    :disableCon="!disabled"
                    @onSubmit="onSubmit">
            Kch hot
        </submit-btn>
        <!-- <a href="/auth/login">ng nhp</a> -->
    </div>
    
</template>

<script>
import submitBtn from '../../components/button/submitBtn.vue'
export default {
    data() {
        return {
            isLoading: false,
            code: ''
        }
    },
    components:{
        submitBtn
    },
    computed:{
        disabled(){
            return this.code.length > 0 ? true : false
        }
    },
    methods:{
        onSubmit(){
            this.isLoading = true;
            fetch(`${process.env.VUE_APP_PORT}/user/activate`,{
                method: 'POST',
                body:JSON.stringify({
                    code: this.code,
                    userId: this.$store.state.signupUserId || 'acais1i2hiuas',
                }),
                headers:{
                    'Content-Type': 'application/json'
                }
            }).then(resp=>{
                this.isLoading = false;
                return resp.json();
            }).then(resData=>{
                if(resData.status === 'success'){
                    this.firePopup('success', 'Thnh cng', 'Kch hot ti khong thnh cng - ang chuyn v trang ng nhp ...');
                    setTimeout(()=>{
                        this.$router.push('/auth/login');
                    }, 2000)
                } else if (resData.status === 'fail'){
                    this.firePopup('error', 'M khng hp l', 'Kch hot ti khong tht bi');
                }
            }).catch(err=>{
                this.firePopup('error', 'Li sever', 'Kch hot tht bi');
                throw err;
            })
        },

        resend(){
            fetch(`${process.env.VUE_APP_PORT}/user/resend`, {
                headers:{
                    'Content-Type': 'application/json',
                },
                method: 'POST',
                body: JSON.stringify({
                    userId: this.$store.state.signupUserId
                })
            }).then(resp=>{
                return resp.json()
            }).then(resData=>{
                if(resData.status === 'fail'){
                    this.firePopup('warning', 'Gi li mail tht bi', 'Li gi mail tht bi t server')
                } else {
                    this.firePopup('success', 'Thnh cng', 'Chng ti  gi li m kch hot cho bn')
                }
            })
        },

        
    }
}
</script>


<style lang="scss">
    .sucess-log{
        background-color: white;
        opacity: .91;
        max-width: 60%;
        padding: 3rem 4rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        @media screen and (max-width: 862px) {
            max-width: 90%;
            padding: 2rem 1.2rem;
        }
        h4, h6{
            text-align: center;
        }
        h6{
            line-height: 1.6rem;
        }
        h4{
            @media screen and (max-width: 600px) {
                font-size: 1.8rem;
            }
        }
        .input-field{
            width: 30rem !important;
            @media screen and (max-width: 862px) {
                width: 100% !important;
            }
        }
        .row .col s6{
            margin-left: 1rem;
        }
    }
</style>

